body {
  font-family: sans-serif;
  margin: 0;
  /* padding: 20px; */
}

h1, h2 {
  text-align: center;
  padding: 0;
  margin: 0;
}

.gallery-list ul {
  list-style: none;
  padding: 0;
}

.gallery-list li {
  margin-bottom: 10px;
}

.image-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); /* Responsive grid */
  gap: 15px;
}

img {
  width: 100%;
}

button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

/*Burger menu*/
/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}


/* Plagination */
.pagination {
  position: fixed;
  bottom: 20px; /* Adjust this value for desired spacing */
  left: 50%;
  transform: translateX(-50%); /* Center horizontally */
  display: flex;
}

.pagination button {
  padding: 8px 15px;
  border: none;
  background-color: #4CAF50; /* Green */
  color: white;
  font-size: 16px;
  cursor: pointer;
}

.pagination select {
  padding: 8px;
  border: 1px solid #ccc;
  font-size: 16px;
}


.pagination button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}




/* ... existing styles ... */

#mySearch {
  padding: 10px;
  border: 2px solid #ccc; /* Light gray border */
  border-radius: 5px; /* Rounded corners */
  font-size: 16px;
  box-sizing: border-box; /* Include padding and border in total width/height */
  max-width: 200px; /* Adjust as needed for desired input width */
  margin: 10px auto; /* Center horizontally */
}

#mySearch::placeholder {
  color: #999; /* Gray placeholder text */
}

#mySearch:focus {
  border-color: #666; /* Darker gray border on focus */
  outline: none; /* Remove default browser focus outline */
}


::-webkit-scrollbar {
  width: 0px;
  background: transparent; /* make scrollbar transparent */
}
